import React from "react"
import { Switch, Route } from "react-router-dom"
import { DailyAttendance, Home, PersonnelList } from "../../pages"

function Routes() {
    return (
        <Switch>
            <Route exact path="spa-react/">
                <Home />
            </Route>
            <Route path="spa-react/personnel-list">
                <PersonnelList />
            </Route>
            <Route path="spa-react/daily-attenance">
                <DailyAttendance />
            </Route>
        </Switch>
    )
}

export default Routes
